.menu {
  display: flex;
  justify-content: center;
  width: 100%;
  background: #1e1e1e38;
  margin-bottom: 30px;
}
.menu .menu-container {
  display: grid;
  grid-template-columns: repeat(5 ,1fr);
  padding: 25px 20px;
  grid-gap: 5px;
}
.menu .menu-container .menu-button {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 8px 15px;
  border: 1px solid crimson;
  outline: none;
  /* margin: 0 5px; */
  cursor: pointer;
  border-radius: 10px;
  background: transparent;
  color: crimson;
  transition: 0.5s;
  font-size: 11px;
  text-decoration: none;
  /* box-shadow: rgba(220, 20, 60, 0.25) 0px 2px 5px -1px,
    rgba(220, 20, 60, 0.3) 0px 1px 3px -1px; */
}
.menu .menu-button:hover {
  color: #f5f5f5;
  background: crimson;
}
.menu .menu-button:last-child {
  background: crimson;
  color: #f5f5f5;
  margin-right: 0;
}
.menu .menu-container .menu-button i {
  margin-right: 5px;
}
.menu .menu-container .menu-button:first-child {
  margin-left: 0;
}

@media(max-width : 900px){
  .menu .menu-container {
  grid-template-columns: repeat(3 ,1fr);
  padding: 15px 10px;
}
}
@media(max-width : 500px){
  .menu .menu-container {
  grid-template-columns: repeat(2 ,1fr);
  padding: 15px 5px;
}
}
@media(max-width : 350px){
  .menu .menu-container {
  grid-template-columns: repeat(1 ,1fr);
  padding: 15px;
}
}