.dashboard {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  /* flex-grow: 1; */
  margin-top: 20px;
}
.dashboard img {
  width: 200px;
  object-fit: cover;
  object-position: center;
}

.dashboard h1 {
  font-size: 30px;
}
.dashboard h2 {
  font-size: 20px;
  margin-bottom: 30px;
}

.dashboard > * {
  margin: 5px;
  padding: 5px;
}

.contact-us {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 25px;
}

.contact-us a {
  text-decoration: none;
  color: #1e1e1e;
  cursor: pointer;
  font-size: 20px;
  margin-bottom: 20px;
}
