.dropdown-wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  max-height: 200px;
  overflow-y: scroll;
  background: #f5f5f5;
  z-index: 200;
}

/* .dropdown-wrapper .dropdown {
  width: 100%;
} */

.dropdown-wrapper .dropdown ul {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
}

.dropdown-wrapper .dropdown ul li {
  display: flex;
  /* justify-content: space-between; */
  padding: 10px;
  cursor: pointer;
}
.dropdown-wrapper .dropdown ul li:nth-child(odd) {
  background: lightgray;
  /* border-radius: 5px; */
}

/* .drop-down-items {
} */

.dropdown-wrapper .dropdown ul li {
  /* width: 100%; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-grow: 1;
  transition: 0.5s;
  /* z-index: 300; */
  /* padding: 5px;
  padding-right: 15px; */
}
.dropdown-wrapper .dropdown ul li:hover {
  background: rgba(220, 20, 60, 0.26);
}
