.get-order-info {
  width: 100%;
  display: flex;
  flex-direction: column;
  /* margin: 10px;
  padding: 10px; */
  position: relative;
}

.get-order-info .section-header .info-header {
  width: 100%;
  margin-top: 40px;
  margin-bottom: 10px;
  border-radius: 15px;
  color: #474747;
}

.get-info-form {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 15px;
  margin-top: 20px;
}

.form-input {
  padding: 11px 8px;
  /* border: 1px solid rgba(128, 128, 128, 0.288); */
  border: none;
  border-radius: 7px;
  outline: none;
  background: transparent;
  font-size: 12px;
  font-family: Poppins;
  color: #1e1e1e;
  transition: 0.5s;
}

.dropdown-parent {
  width: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
  margin-bottom: 40px;
}

.dropdown-wrapper {
  display: flex;
  flex-direction: column;
  z-index: 2000;
}
.form-input-dropdown-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  outline: none;
  font-size: 12px;
  font-family: Poppins;
  color: #1e1e1e;
  transition: 0.5s;
  cursor: pointer;
  border: 1px solid rgba(128, 128, 128, 0.288);
  border-radius: 7px;
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  padding: 1px;
}
.form-input-dropdown-wrapper-date {
  /* display: flex ; */
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0 10px;
  color: #1e1e1e;
}
.dropdown-content {
  display: flex;
  border: none;
  width: 100%;
  flex-direction: column;
  cursor: pointer;
  /* border: 1px solid rgba(128, 128, 128, 0.288); */
  border-radius: 0px 0px 7px 7px;
  overflow: hidden;
  /* background: #f5f5f5; */
  /* overflow: hidden; */
}

.form-input-dropdown-wrapper-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  position: relative;
  justify-content: flex-end;
  padding: 5px 8px;
}
.form-input-dropdown-wrapper-header .form-input-dropdown {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
.form-input-dropdown-wrapper i {
  display: flex;
  align-items: center;
  /* justify-content: flex-end !important; */
  padding: 10px;
  pointer-events: none;
}
.form-input-dropdown {
  width: 100%;
  border: none;
  outline: none;
  flex-grow: 1;
  margin-left: 10px;
  /* padding: 13px 8px; */
  background: transparent;
  background-color: white;

  cursor: pointer;
}

.form-size-wrapper {
  width: 100%;
  grid-column: 1/-1;
  display: grid;

  grid-template-columns: repeat(2, 1fr);
  grid-gap: 40px;
  margin-bottom: 20px;
}

.form-size-wrapper .form-size {
  /* background-color: red; */
  display: flex;
  /* align-items: center; */
  border: none;
  padding: 0;
  font-size: 20px;
  margin-top: 20px;
}

.get-info-form .form-size > input {
  width: 100%;
  /* width: 50%; */
  /* height: 28px; */
  margin-right: 5px;
  padding: 12px 8px;
  border-radius: 7px;
  outline: none;
  background: transparent;
  border: 1px solid rgba(128, 128, 128, 0.288);
  font-size: 12px;
  font-family: Poppins;
  color: #1e1e1e;
  transition: 0.5s;
}

.get-info-form .form-size input:first-child {
  /* width: 100%; */
}
.get-info-form .form-size .dropdown-parent {
  margin-left: 5px;
  /* margin-left: 5px;
  margin-right: 0; */
  /* padding: 0;
  margin: 0; */
}
.get-info-form .form-size .form-input-dropdown-wrapper {
  outline: none;
  padding: 0;
  margin: 0;

  /* border: none !important; */
}

.form-button {
  grid-column: 1 / -1;
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  padding: 0;
  border: none;
  height: 40px;
}

.get-info-form .form-button button {
  border: none;
  color: #f5f5f5;
  font-size: 14px;
  cursor: pointer;
  transition: 0.5s;
  border-radius: 10px;
  text-transform: uppercase;
  font-family: Poppins;
}

.get-info-form button:hover {
  opacity: 60%;
}
.get-info-form .button-reset {
  background-color: crimson;
}

.get-info-form .button-submit {
  background-color: green;
}

.get-info-form > *:hover {
  border-color: rgb(220, 20, 60, 0.4);
}

.get-info-form > * :focus {
  border-color: rgb(220, 20, 60, 0.4);
}
.get-info-form .form-size > *:hover {
  border-color: rgb(220, 20, 60, 0.4);
}
.get-info-form .form-size > *:focus {
  border-color: rgb(220, 20, 60, 0.4);
}

.get-info-form .dropdown-parent > *:hover {
  border-color: rgb(220, 20, 60, 0.4);
}

.get-info-form .dropdown-parent > * :focus {
  border-color: rgb(220, 20, 60, 0.4);
}

@media (max-width: 768px) {
  .get-info-form {
    grid-template-columns: repeat(2, 1fr) !important;
  }
}
@media (max-width: 481px) {
  .get-info-form {
    grid-template-columns: repeat(1, 1fr) !important;
  }

  .form-size-wrapper {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 10px;
  }
}
