.customer-information {
  display: flex;
  flex-direction: column;
  padding: 0 10px;
}

.customer-information .information {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  font-size: 13px;
  font-weight: 400;
}
.customer-information .information strong {
  font-weight: 500;
}
.customer-information .information .info-left,
.customer-information .information .info-right {
  display: flex;
  flex-direction: column;
}

.invoice-header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px;
  border-bottom: 2px solid gray;

  font-size: 15px;
}

.invoice-header img {
  width: 120px;
  margin: 7px 0;
}
