.invoice {
  /* display: flex;
  justify-content: center;
  align-items: center; */
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  margin: 20px 20px;
  padding: 25px 0px;
  border-radius: 20px;
  margin-bottom: 100px;
  width: 100%;
  max-width: 100%;
}

.invoice-wrapper {
  /* overflow-x: scroll; */
}

.header-title {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  /* margin-bottom: 20px; */
}
.info-header {
  width: 100%;
  text-align: start;
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 20px;
}
.header-title i {
  cursor: pointer;
  transition: 0.7s;
  margin-right: 20px;
  padding: 10px;
}

.header-title i:hover {
  color: crimson;
}

.other-amounts {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 10px;
  margin: 10px 0;
  font-size: 13px;
}

.discount-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid lightgray;
  border-radius: 10px;
  width: 100%;
  padding: 10px 25px;
}
.discount-wrapper input {
  width: 75px;
  padding: 5px;
  border: none;
  outline: none;
  border-bottom: 1px solid lightgray;
}
.discount-wrapper div {
  display: flex;
  align-items: center;
}
.discount-wrapper label {
  flex-grow: 1;
}

.export-wrapper {
  width: 100%;
  display: flex;
  /* align-items: center; */
}
.payments,
.export {
  width: 100%;
  padding: 12px;
  border-radius: 10px;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 20px;
  border: 1px solid lightgray;
  margin: 15px 0;
  align-items: center;
  font-size: 13px;
}
.export {
  grid-template-columns: repeat(3, 1fr);
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
}

.export-type-action {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100px;
  min-height: 100%;
  margin: 15px 0;
}
.export-type-action button {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid crimson;
  outline: none;
  color: crimson;
  height: 100%;
  background-color: transparent;
  border-radius: 10px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  cursor: pointer;
  transition: 0.5s;
}
.export-type-action button:hover {
  background: crimson;
  border: 1px solid crimson;
  color: white;
}
.payment-card,
.export-type {
  display: flex;
  justify-content: start;
  align-items: center;
  cursor: pointer;
  transition: 0.5s;
}
.export-type > div {
  display: flex;
  align-items: center;
}
.export-type-button {
  width: 100%;
  display: flex;
  align-items: center;
  height: 100%;
}
.export-type-button button {
  width: 100%;
  height: 100%;
}
.payment-card:hover,
.export-type:hover {
  color: crimson;
}

.payment-card:hover input,
.export-type:hover input {
  background-color: crimson;
}

.payment-card label,
.export-type label {
  padding: 0 10px;
}

/*  responsive /////////////////////////////////////////////////////////////// */
@media (max-width: 1250px) {
  .other-amounts {
    grid-template-columns: repeat(4, 1fr);
  }
  .payments {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 1000px) {
  .other-amounts {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 780px) {
  .other-amounts {
    grid-template-columns: repeat(2, 1fr);
  }
  .payments {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 600px) {
  .other-amounts {
    grid-template-columns: repeat(1, 1fr);
  }
  .payments {
    grid-template-columns: repeat(2, 1fr);
  }
  .export {
    grid-template-columns: repeat(1, 1fr);
  }
}
